@import '../../sass/variabul' ; 
.header-content{
  display: flex;
  padding: 15px;
  justify-content: space-between;
  background-color: $header-color;
  .logo{
    font-size: 22px;
    font-weight: bold;
    color: $supText-color;
    text-transform: capitalize;
  }
  .log{
    font-size: 20px;
    padding: 0 7px;
    background-color: transparent;
    color: $btn-color;
    border: none;
    border: 2px solid $btn-color;
  }
}