@import '../../sass/variabul' ;
.form-content{
  width: 50%;
  margin: 15px auto;
  display: flex;
  flex-direction: column;
  row-gap: 5px;
  box-shadow: 0 0 10px 5px #AAA;
  padding: 15px;
  .form-title{
    text-transform: capitalize;
  }
  .label{
    text-transform: capitalize;
  }
  .input{
    width: 90%;
    font-size: 18px;
    padding: 5px;
  }
  textarea{
    height: 50px;
  }
  button{
    width: fit-content;
    padding: 10px;
    background-color: $btn-color;
    color: white;
    font-size: 18px;
    font-weight: bold;
    border-radius: 10px;
    border: none;
  }
}