@import '../../sass/variabul';

.book-list-content{
  width: 400px;
  h2{
    text-transform: capitalize;
    margin-bottom: 7px;
  }
  .book-box{
    border: 1px solid #AAA;
    font-size: 20px;
    > div{
      display: flex;
      justify-content: space-between;
      padding: 10px;
      &:not(:last-of-type){
        border-bottom: 1px solid #AAA;
        
      }
      .btns{
        button{
          padding: 5px;
          color: white;
          border-radius: 5px;
          border: none;
        }
        .read{
          background-color:$btn-color ;
          margin-right: 5px;
        }
        .del{
          background-color: $delet-color;
        }
      }
    }
  }
}